.inner {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.slick-next,
.slick-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 50;
  cursor: pointer;
  font-size: 0;
  width: 34px;
  height: 60px;
}

.slick-next:after, .slick-next:before,
.slick-prev:after,
.slick-prev:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slick-prev:before, .slick-prev:after {
  margin-left: -9px;
}

.slick-prev:after {
  left: 60%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 18px 17px 0;
  border-color: transparent #dcdcdc transparent transparent;
}

.slick-prev:before {
  left: 50%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 18px 17px 0;
  border-color: transparent #000 transparent transparent;
}

.slick-next:before, .slick-next:after {
  margin-right: -9px;
}

.slick-next:after {
  right: 60%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 0 17px 18px;
  border-color: transparent transparent transparent #dcdcdc;
}

.slick-next:before {
  right: 50%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 0 17px 18px;
  border-color: transparent transparent transparent #000;
}

.slick-next {
  right: 10px;
}

.slick-prev {
  left: 10px;
}

