.menu.menu_footer .menu__link:hover {
  text-decoration: none;
}

.breadcrumbs {
  font-size: 0;
  padding: 10px 0;
  margin-bottom: 20px;
}

@media only screen and (max-width: 1023px) {
  .breadcrumbs {
    display: none;
  }
}

.breadcrumbs__item {
  display: inline-block;
}

.breadcrumbs__item:last-child .breadcrumbs__link {
  text-decoration: none;
  cursor: default;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 26px;
  position: relative;
}

.breadcrumbs__item:not(:last-child):after {
  content: '/';
  display: block;
  position: absolute;
  top: 50%;
  font-size: 14px;
  transform: translateY(-50%);
  right: -16px;
  color: #767676;
}

.breadcrumbs__link {
  text-decoration: underline;
  display: block;
  cursor: pointer;
  font-size: 14px;
  color: #767676;
  letter-spacing: 0.025em;
}

.pagination {
  padding-bottom: 65px;
  margin-bottom: 30px;
  border-bottom: 1px solid #767676;
  font-size: 0;
  text-align: center;
}

@media only screen and (max-width: 1023px) {
  .pagination {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 599px) {
  .pagination {
    padding-bottom: 40px;
  }
}

@media only screen and (max-width: 480px) {
  .pagination {
    padding-bottom: 20px;
  }
}

.pagination__item {
  display: inline-block;
  margin: 0 10px;
}

.pagination__link {
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 22px;
  display: block;
  color: #767676;
}

.pagination__link.is-current {
  font-size: 18px;
  color: #777777;
  font-weight: 400;
}

.input {
  border: 1px solid #c3c3c3;
  transition: border-color .24s ease-in-out, box-shadow .24s ease-in-out, color .24s ease-in-out;
  height: 42px;
  line-height: normal;
  padding: 0 15px;
}

.input.is-error {
  color: #dc7f7f;
  font-style: italic;
  border-color: #dc7f7f;
  box-shadow: inset 0 0 13px 1px rgba(228, 106, 106, 0.75);
}

.input.is-error::placeholder {
  color: #dc7f7f;
}

.label {
  margin-bottom: 5px;
  color: #535353;
  line-height: 1;
  display: block;
  font-size: 14px;
}

.textarea {
  resize: none;
  padding: 15px;
  width: 100%;
}

.form-control {
  font-size: 0;
}

.form-control:not(:last-child) {
  margin-bottom: 20px;
}

.form__buttons {
  text-align: center;
}

.form__buttons .btn {
  font-size: 17.77px;
  color: #fff;
  background-color: #777777;
  font-weight: 700;
  padding: 13px 16px;
  line-height: 1;
  text-transform: uppercase;
  transition: background-color .24s ease-in-out;
}

.form__buttons .btn:hover {
  background-color: #1a1a1a;
}

.form .input {
  width: 100%;
}

.form .input::placeholder {
  font-family: "PT Sans";
}
